import './AboutUs.css';
import React from 'react';
import { motion, spring } from "framer-motion";
import BildKubernetes from './TechStackGraphics/Kubernetes_logo_without_workmark.svg.png';
import PodmanBild from './TechStackGraphics/podman-logo.png';
import RustBild from './TechStackGraphics/rust-logo-7072B9D560-seeklogo.com.png';
import LinuxBild from './TechStackGraphics/Almalinux-logo-white-v2_jzMVHfK.png';
import PostgreBild from './TechStackGraphics/PostgreSQL-Logo.png';
import ReactBild from './TechStackGraphics/React-icon.svg.png';
import HtmlBild from './TechStackGraphics/174854.png';
import CssBild from './TechStackGraphics/css-3-logo-023C1A7171-seeklogo.com.png';
import JsBild from './TechStackGraphics/JavaScript-logo.png';
import TsBild from './TechStackGraphics/Typescript_logo_2020.svg.png';
import WebPackBild from './TechStackGraphics/icon-square-big.png';
import fish from'./TechStackGraphics/220px-Arothron_meleagris_by_NPS_1.jpg';
import cat from './TechStackGraphics/weisse-Katzen.jpg';
import fox from './TechStackGraphics/adobestock301516754_0.jpeg';
import blur from './DesignAppsGraphics/App_Bubbles.png';

const AboutUs: React.FC = () => {
   return (
      <div>
         <body>
            <span className='above'>
               <motion.h2 initial={{opacity: 0}} animate={{opacity: 1}} transition={{delay: 0, duration: 2}}>About Us</motion.h2>
               <motion.h1 initial={{opacity: 0}} animate={{opacity: 1}} transition={{delay: 0.5, duration: 2}}>Lernen Sie uns kennen!</motion.h1>
               <motion.p initial={{opacity: 0}} animate={{opacity: 1}} transition={{delay: 1, duration: 2}}>
                  Hier beantworten wir Ihnen alle Fragen, die Sie ber uns haben knnten.
                  Damit wir nicht mehr allzu anonym auf Sie wirken.
               </motion.p>
            </span>
               
            <section className='first' id='wer-sind-wir'>
               <motion.div className="text"
               initial={{ opacity: 0, x: -100 }}
               whileInView={{ opacity: 1, x: 0 }}
               transition={{ ease: "easeOut", duration: 1 }}
               >
               <h1>Wer sind wir?</h1>
               <p>Wir sind eine kleine Gruppe, die sich zusammengetan hat, um Webseiten zu erstellen. Die Idee entstand zu unserer Studienzeit und wuchs immer stetig weiter. So wurde schnell aus einer kleinen Idee eine Vielzahl an Stunden, in denen wir versuchten unseren Traum eine eigene Firma zu grnden, zu verwirklichen. So kristallisierte sich aus dieser kleinen Idee "Asynchrone", eine Firma aus fnf Freunden.</p> </motion.div>
                  <motion.div className="imgcontainer"
                  initial={{ opacity: 0}}
                  whileInView={{ opacity: 1}}
                  transition={{ ease: "easeOut", duration: 1 }}
                  >
                     <img src={fish} alt="pic2"
                        width="40%"
                     /> 
                     <img src={cat} alt="pic1" 
                        width="30%"
                        /> 
                     <img src={fox} 
                     width="20%"
                     
                     /> 
                  </motion.div>
            </section>
         <div className="background">
         <div className='secondbackground'>
         <motion.div
           initial={{ opacity: 0, x: -100 }}
           whileInView={{ opacity: 1, x: 0 }}
           transition={{ ease: "easeOut", duration: 2 }}
         >
            <section className='second' id='was-machen-wir'>
               <div className="text">
                  <h1>Was machen wir?</h1>
                  <p>Unsere derzeitige Prioritt liegt an der Erstellung von Websites. Wir legen groen Wert auf Qualitt und versuchen unsere Designs so gut wie mglich nach Ihren Ideen anzupassen. Wir bilden uns immer weiter, um unseren Workflow zu beschleunigen und die Qualitt noch weiter hochzuschrauben.</p></div>
            </section>
            </ motion.div>
            </div>
            
            <section className='TechStack' id='technik'>
               <div className="list">
                  <motion.div initial={{opacity: 0, x: '-30vw'}} whileInView={{opacity: 1, x: 0}} transition={{delay: 0, duration: 2}} className="inlinelist">
                     <div className="left1"></div>
                     <button><img src= {BildKubernetes}
                        alt="html" 
                        width="50px"/><p>Kubernetes</p></button>
                     <button><img src= {PodmanBild}
                        alt="html" 
                        width="175px"
                        className='extrawidth'/></button>
                     <button><img src= {RustBild}
                        alt="html" 
                        width="100px"
                        className='extrawidth'/></button>
                  </motion.div>
                  <motion.div initial={{opacity: 0, x: '-30vw'}} whileInView={{opacity: 1, x: 0}} transition={{delay: 0.2, duration: 2}} className="inlinelist">
                     <button><img src= {LinuxBild}
                        alt="html" 
                        width="175px"
                        className='extrawidth'/></button>
                     <button><img src= {PostgreBild}
                        alt="html" 
                        width="200px"
                        className='extrawidth'/></button>
                     <button><img src= {ReactBild}
                        alt="html" 
                        width="50px"/><p>React</p></button>
                  </motion.div>
                  <motion.div initial={{opacity: 0, x: '-30vw'}} whileInView={{opacity: 1, x: 0}} transition={{delay: 0.4, duration: 2}} className="inlinelist">
                     <div className="left3"></div>
                     <button><img src= {HtmlBild}
                        alt="html" 
                        width="50px"/><p>HTML</p></button>
                     <button><img src= {CssBild}
                        alt="html" 
                        width="45px"/><p>CSS</p></button>
                     <button><img src= {JsBild}
                        alt="html" 
                        width="50px"/><p>JavaScript</p></button>
                  </motion.div>
                  <motion.div initial={{opacity: 0, x: '-30vw'}} whileInView={{opacity: 1, x: 0}} transition={{delay: 0.6, duration: 2}} className="inlinelist">
                     <div className="left4"></div>
                     <button><img src= {TsBild}
                        alt="html" 
                        width="50px"/><p>TypeScript</p></button>
                     <button><img src= {WebPackBild}
                        alt="html" 
                        width="75px"/><p>Webpack</p></button>
                  </motion.div>
                  <motion.div initial={{opacity: 0, x: '-30vw'}} whileInView={{opacity: 1, x: 0}} transition={{delay: 0.8, duration: 2}} className="inlinelist">
                     <div className="left5"></div>
                     <button className='unused'></button>
                     <button className='unused'></button>
                     <button className='unused'></button>
                  </motion.div>
                  <motion.div initial={{opacity: 0, x: '-30vw'}} whileInView={{opacity: 1, x: 0}} transition={{delay: 1, duration: 2}} className="inlinelist">
                     <div className="left6"></div>
                     <button className='unused'></button>
                     <button className='unused'></button>
                  </motion.div>
               </div>
               <div className="text">
               <p>Technik</p>
               <h1 className="bigger">Unser<br />Tech Stack</h1>
               </div>
            </section>

         </div>
         <motion.div
           initial={{ opacity: 0, x: -100 }}
           whileInView={{ opacity: 1, x: 0 }}
           transition={{ ease: "easeOut", duration: 2 }}
           id='design'
         >
            <div className="unsereDesignApps">
               <div className="text">
                  <p>Design</p>
                  <h1>Unsere Design Apps</h1>
               </div>
               <div className="designstack"><img src={blur} alt="blur"
               width="80%" /></div>
            </div>
            </motion.div>
         </body>
      </div>
   );
}

export default AboutUs;