import './Startpage.css';
import Karussell from './Karussell/karussellWithText';
import Faq from './FAQ/Faq';
import Tabs from './Tabs/TabsWithText';
import Hero from './Hero/Hero';
import Price from './Price/price';

function Startpage() {
  return (
    <div className='body'>
      <div id='warum-wir'>
        <Hero />
      </div>
      <div className='main karussell' id='vorlagen'>
        <Karussell />
      </div>
      <div className='main tabs' id='ablauf'>
        <Tabs />
      </div>
      <div className="main price">
        <Price />
      </div>
      <div className='main faq' id='faq'>
        <Faq />
      </div>
    </div>
  );
}

export default Startpage;
